.jumbotron{
    width: 100%;
    min-height: 300px;
    padding-top: 100px;
    margin: 0px;
    margin-bottom: 5%;
    display: table;
    padding: 180px 20px;
    color: white;
    background-color: grey;
}
div.thumbnail {
background-size: cover;
background-repeat: no-repeat;
background-position: center;
width: 100%;
padding-top: 100%;
}
#search,
#saved {
    color: white;
}

#search:hover,
#saved:hover {
    color: red;
}

#nav {
    background-color:black;
}
